.secondMain {
    font-family: 'Courier New', monospace;
    font-weight: bold;
    animation: slideIn 1.5s ease-out;
    transition: font-size 0.5s;
}
.firstone{
    text-align: justify;
    font-size: 17px;
    color: rgb(84, 84, 84);

}

.thirdMain {
    font-family: 'Courier New', monospace;
    font-weight: bold;
    animation: bounceIn 2s ease-out;
    transition: font-size 0.5s;
    text-align: justify;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@keyframes slideIn {
    from {
        transform: translateY(-20px);
        opacity: 0;
    }

    to {
        transform: translateY(0);
        opacity: 1;
    }
}

@keyframes bounceIn {

    0%,
    20%,
    40%,
    60%,
    80%,
    100% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-20px);
    }
}


